import NosTerrains from '../visiteur/NosTerrains'
import NosResultats from '../visiteur/NosResultats'
// import NosContacts from '../visiteur/NosContacts'
import InscriptionTournoiEcole from '../visiteur/InscriptionTournoiEcole'
import DetailTournoiEcole from '../visiteur/DetailTournoiEcole'
