const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class AccueilContent extends Component {
  constructor(props) {
      super(props)
      this.state = {
        textApropos:"",
        ok:"",
        titreApropos:"",
        idApropos:0,
        telephone:"",
        email:"",
        adresse:"",
        service:[],
        video_joutube:"",
        video:"",
        file:[],
        home:[],
        terrain:[],
        tournoi_ecole:[],
        match_joue_entreprise:[],
        match_joue_ecole:[],
        tournoi_entreprise:[],
        pub:{},
        fichier:[],
        detailModal: false,
        detail_terrain:{},
        page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
        <Spinner type="grow" color="success" />
     </div>,
        status:true,
        resultatEcole:{},
        status_resultat_ecole:false,
        status_resultat_entreprise:false,
        resultatEntreprise:{}
      }   
      this.handleImage.bind(this)
      this.handleTerrainClick.bind(this)
      this.handleReserverTerrain.bind(this)
      this.handleButton.bind(this)
      this.handleParticiperTournoisEcole.bind(this)
      this.handleParticiperTournoisEntreprise.bind(this)
      this.handleDetailTournoisEntreprise.bind(this)
      this.handleDetailTournoisEcole.bind(this)
  }

  charging=()=>{
    $("#msg").css("display","none")
    $("#chargement").css("display","none")
  }
  
  componentDidMount() {
    localStorage.removeItem('idEquipePaye');
    localStorage.removeItem('idReservationVisiteur');
    localStorage.removeItem('idTransactionVisiteur');
    this.charging()
    
   // ReactDOM.render(<div>{<DetailTournoiEcole/>}</div>, document.getElementById('page'));
   
   // this.setState({page:<Spinner type="grow" color="secondary" />})
    console.log(new Date())
    let  data=  JSON.parse(localStorage.getItem('token_auth'))

    const requestOptions = {
      method: 'GET',
  
    };

    fetch(conf.url+"acceuil/propos/propos",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("propos====>",data)
      try{
        this.setState({titreApropos:data[0].titre})
        this.setState({textApropos:data[0].text})
        this.setState({adresse:data[0].adresse})
        this.setState({telephone:data[0].telephone})
        this.setState({email:data[0].email})
        this.setState({video_joutube:data[0].urlVideo})
        this.setState({idApropos:data[0].id})
        localStorage.setItem('contact', JSON.stringify({telephone:data[0].telephone,email:data[0].email}))
      }catch(error){

      }
      
    }
   
  );

  fetch(conf.url+"acceuil/homeone/homeones",requestOptions)
  .then((response) => response.json())
  .then((data) => {
    console.log("home====>",data)
     
     try{

      if(data.length){
      this.setState({home:data})
       this.handleImage(data)
      }
     }catch(error){
      this.setState({home:[]})
     }
     // this.handleImage(data)
      
    
   }
   
  );

  fetch(conf.url+"acceuil/service/services",requestOptions)
  .then((response) => response.json())
  .then((data) => {
    console.log("serviii===>",data)
    this.setState({service:data})
    try{

      if(data.length){
        
        data.map((el,idx)=>{
          if(el.multimedia.file){
            storage.ref("images")
            .child(el.multimedia.file).getDownloadURL()
            .then((url) => {
                
                data[idx].multimedia.file=url
                this.setState({service:data})
            
            })
        }
        })
      }
     }catch(error){
      this.setState({service:[]})
     }
     
    
   }
   
  );

  fetch(conf.url+"tournoi/tournois/typeTournoi/"+conf.type_tournoi.TOURNOI_INTER_ECOLE,requestOptions)
  .then((response) => response.json())
  .then((data) => {
   console.log("ecole",data)

   var new_data=data.filter((el)=>(new Date(el.dateDebutInscription)<=new Date()) && (new Date(el.dateFinInscription)>=new Date()))
   console.log("entreprise filre",new_data)
   this.setState({tournoi_ecole:new_data})
   new_data.map((el,idx)=>{
     try{
       if(el.multimedia.file!=undefined && el.multimedia.file!=''){
         storage.ref("images")
         .child(el.multimedia.file).getDownloadURL()
         .then((url) => {
           
           new_data[idx].multimedia.file=url
             this.setState({tournoi_ecole:new_data})
         
         })
         }
     }catch(error){

     }
   })



    try{
      
      if(data[0].matchs.length){
        this.setState({resultatEcole:data[0].matchs.find(element=>element.matchDejaJoue===true)})
        if(data[0].matchs.find(element=>element.matchDejaJoue===true).matchDejaJoue){
          this.setState({status_resultat_ecole:true})
        }
      }
      
    }catch(error){
      this.setState({resultatEcole:{}})
      this.setState({status:false})
    }

    this.charging()
   }
   
   );

   fetch(conf.url+"terrain/terrains",requestOptions)
   .then((response) => response.json())
   .then((data) => {
     console.log("data terrain",data)
     this.setState({status:false})
     try{

      if(data.length){
        this.setState({status:false})
        data.filter((val)=>val.statusTerrain!="PUBLIC").map((el,idx)=>{
          storage.ref("images")
          .child(el.multimedias[0].file).getDownloadURL()
          .then((url) => {
              
              data[idx].multimedias[0].file=url
              this.setState({terrain:data})
          
          })
        })
       
      }
     }catch(error){
      this.setState({terrain:data.filter((val)=>val.statusTerrain!="PUBLIC")})
     }
     
 
     })

  fetch(conf.url+"tournoi/tournois/typeTournoi/"+conf.type_tournoi.TOURNOI_INTER_ENTREPRISE,requestOptions)
  .then((response) => response.json())
  .then((data) => {
    
    var new_data=data.filter((el)=>(new Date(el.dateDebutInscription)<=new Date()) && (new Date(el.dateFinInscription)>=new Date()))
    this.setState({tournoi_entreprise:new_data})
    new_data.map((el,idx)=>{
      try{
        if(el.multimedia.file!=undefined && el.multimedia.file!=''){
          storage.ref("images")
          .child(el.multimedia.file).getDownloadURL()
          .then((url) => {
            
            new_data[idx].multimedia.file=url
         
              this.setState({tournoi_entreprise:new_data})
          
          })
          }
      }catch(error){
       console.log("error entreprise",error)
      }
    })
    
    
    try{
     
      if(data[0].matchs.length){
        this.setState({resultatEntreprise:data[0].matchs.find(element=>element.matchDejaJoue===true)})
        if(data[0].matchs.find(element=>element.matchDejaJoue===true).matchDejaJoue){
          this.setState({status_resultat_entreprise:true})
        }
      }
      
    }catch(error){
      this.setState({resultatEntreprise:{}})
      // this.setState({tournoi_entreprise:data})
    }
    
   }
   
   );

   fetch(conf.url+"acceuil/pub/pubs",requestOptions)
   .then((response) => response.json())
   .then((data) => {
     console.log("home====>",data)
     try{
      var pub=data[Math.floor(Math.random() * data.length)]
      storage.ref("images")
      .child(pub.multimedia.file).getDownloadURL()
      .then((url) => {
          
          pub.multimedia.file=url
          this.setState({pub:pub})
      
      })
     }catch(error){
      this.setState({pub:{}})
     }
     
    })
     
     
    fetch(conf.url+"acceuil/fichier/fichiers",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("fichier====>",data)
    
      
       data.map((el,idx)=>{
         try{
            if(el.multimedia){
            storage.ref("images")
            .child(el.multimedia.file).getDownloadURL()
            .then((url) => {
                if(el.typeFichier==="LOGO_PARTENAIRE"){
                  data[idx].multimedia.file=url
                  this.setState({fichier:data})
                }else{
                  if(el.typeFichier==="VIDEO_PRESENTATION"){
                    this.setState({video:url})
                  }
                }
               
            
            })
            }
      }catch(error){
        this.setState({fichier:data})
      }
       })
      
      
          
      
     }
     
    );
         
  

  }


  handleImage=(data=[])=>{

    data.map((el,idx)=>{
      if(el.multimedia!=null){
      storage.ref("images")
      .child(el.multimedia.file).getDownloadURL()
      .then((url) => {
        
          data[idx].multimedia.file=url
          this.setState({home:data})
      
      })
      }
     })

  }

 handleSendMessage=()=>{
    $("#chargement").css("display","block")
  
    $("#initial").css("display","none")

    var data= {
      "name": $("#nom").val(),
      "email": $("#email").val(),
      "subject":$("#objet").val(),
      "message":$("#message").val(),
    
    }
    
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
    
    },
      body: JSON.stringify(data)
  };

  fetch(conf.url+"acceuil/user/contacter",requestOptions)
 
  .then((data) => {
    

    $("#chargement").css("display","none")
 
    $("#initial").css("display","none")

    $("#msg").css("display","block")

    $("#nom").val("")
    $("#email").val("")
    $("#subject").val("")
    $("#message").val("")
    
   }
   
  );

 }
changePage=()=>{
  this.setState({page:<Apropos/>})
  console.log("oooooooooo")
}

  handleDetailTournoisEcole=(id)=>{
    if(this.state.tournoi_ecole.length){
      var logo=""
      try{

        if(this.state.tournoi_ecole[id].multimedia.file!=undefined){
          storage.ref("images")
          .child(this.state.tournoi_ecole[id].multimedia.file).getDownloadURL()
          .then((url) => {
          
              logo=url
          
          })
          }
      }catch(error){

      }
      
      localStorage.setItem('detail_tournoi_visiteur', JSON.stringify({logo:logo,matchs:this.state.tournoi_ecole[id].matchs,id:this.state.tournoi_ecole[id].id,date_debut:this.state.tournoi_ecole[id].dateDebut,
        date_fin:this.state.tournoi_ecole[id].dateFin,date_fin_inscription:this.state.tournoi_ecole[id].dateFinInscription,prix:this.state.tournoi_ecole[id].prixPartSa,date_debut_inscription:this.state.tournoi_ecole[id].dateDebutInscription,description:this.state.tournoi_ecole[id].description}))

        
     }
    
     ReactDOM.render(<div>{<DetailTournoiEcole/>}</div>, document.getElementById('page'));
    console.log("mon tournois")
  }

handleDetailTournoisEntreprise=(id)=>{
  if(this.state.tournoi_entreprise.length){
    var logo=""
    try{

      if(this.state.tournoi_entreprise[id].multimedia.file!=undefined){
        storage.ref("images")
        .child(this.state.tournoi_entreprise[id].multimedia.file).getDownloadURL()
        .then((url) => {
        
            logo=url
        
        })
        }
    }catch(error){

    }
    
        localStorage.setItem('detail_tournoi_visiteur', JSON.stringify({logo:logo,matchs:this.state.tournoi_entreprise[id].matchs,id:this.state.tournoi_entreprise[id].id,date_debut:this.state.tournoi_entreprise[id].dateDebut,
        date_fin:this.state.tournoi_entreprise[id].dateFin,date_fin_inscription:this.state.tournoi_entreprise[id].dateFinInscription,prix:this.state.tournoi_entreprise[id].prixPartSa,date_debut_inscription:this.state.tournoi_entreprise[id].dateDebutInscription,description:this.state.tournoi_entreprise[id].description}));


   }
  
   ReactDOM.render(<div>{<DetailTournoiEntreprise/>}</div>, document.getElementById('page'));
  
   
   
}
handleParticiperTournoisEcole=(idx)=>{
  let id=0;
  if(this.state.tournoi_ecole.length){
   id=this.state.tournoi_ecole[idx].id
  }
   localStorage.setItem('idAcceuilTournoi',id);
   localStorage.setItem('prixTournoi',this.state.tournoi_ecole[idx].prixPartSa);
   ReactDOM.render(<div>{<InscriptionTournoiEcole/>}</div>, document.getElementById('page'));
}

handleParticiperTournoisEntreprise=(idx)=>{
  let id=0;
  if(this.state.tournoi_entreprise.length){
   id=this.state.tournoi_entreprise[idx].id
  }
   localStorage.setItem('idAcceuilTournoi',id);
   localStorage.setItem('prixTournoi',this.state.tournoi_entreprise[idx].prixPartSa);

  ReactDOM.render(<div>{<InscriptionTournoiEntreprise/>}</div>, document.getElementById('page'));
}
detailTournois=()=>{
  this.setState({status:true,page:<NosTournois/>})
}


toggle() {
  this.setState(prevState => ({
    modal: !prevState.modal,
  }))
}

handleTerrainClick = (data) => {

  this.setState({detailModal:true,detail_terrain:data})
  
  this.toggle()
}

handleReserverTerrain=(data)=>{
  localStorage.setItem('idReservationTerrain',data.id);

  

  var acces=JSON.parse(localStorage.getItem('token_auth'))
  try{
    if(acces.user){
      console.log("usssss",acces.user)
      ReactDOM.render(<div>{<ReservationTerrainVisiteur terrain={data.nom}/>}</div>, document.getElementById('page'));
    }else{
      console.log("usssss nonz")
      localStorage.setItem('connectReserver',data.nom);
      //ReactDOM.render(<div>{<Login/>}</div>, document.getElementById('page'));
      document.getElementById('login').click()
    }


  }catch(error){
      localStorage.setItem('connectReserver',data.nom);
      //ReactDOM.render(<div>{<Login/>}</div>, document.getElementById('page'));
      document.getElementById('login').click()
  }


}
handleVoirTerrain=()=>{
  
  ReactDOM.render(<div>{<NosTerrains/>}</div>, document.getElementById('page'));
}

handleButton=(e)=>{
  console.log("bouton",e)
   if(e==="Rserver"){
     this.handleVoirTerrain()
   }else{
     if(e==="Participer"){
      ReactDOM.render(<div>{<NosTournois/>}</div>, document.getElementById('page'));
     }
   }
}

handleResultat=()=>{
  
  ReactDOM.render(<div>{<NosResultats/>}</div>, document.getElementById('page'));
}
  render() {
  const lienPub="https://www.instagram.com/oterrain.foot/"
  const { isEdit, detailModal } = this.state
  return (
    <>
   
    <CContainer fluid>


    <Suspense fallback={loading}>
    {
    (!this.state.status)?

    (<CFade id="page1">
     {this.state.ok}
      
        <ModalDetailTerrain
          show={this.state.detailModal}
          terrain={this.state.detail_terrain}
          type="TERRAIN"
          onCloseClick={() => this.setState({ detailModal: false })}
         />
    
      {/* Carousel */}
      <section id="hero" >
     
        <div id="heroCarousel" class="carousel slide carousel-fade" data-ride="carousel">

          <ol class="carousel-indicators" id="hero-carousel-indicators"></ol>

          <div class="carousel-inner" role="listbox">
         {
            this.state.home.map((element,idx)=>(

             (idx==0)?(<div class="carousel-item active" 
              // style="background-image: url(../assets/img/slide/foot-salle.png)"
              style={{backgroundImage:`url(${element.multimedia.file})`}}
              >
                <div class="carousel-container">
                  <div class="container">
                    <h2 class="animate__animated animate__fadeInDown">{element.titre}</h2>
                    <p class="animate__animated animate__fadeInUp">{element.description}</p>
                    <a onClick={this.handleButton.bind(this,element.bouton)}  class="btn-get-started animate__animated animate__fadeInUp scrollto a">{element.bouton}</a>
                  </div>
                </div>
              </div>):(
                <div class="carousel-item" 
              // style="background-image: url(../assets/img/slide/foot-salle.png)"
              style={{backgroundImage:`url(${element.multimedia.file})`}}
              >
                <div class="carousel-container">
                  <div class="container">
                    <h2 class="animate__animated animate__fadeInDown">{element.titre}</h2>
                    <p class="animate__animated animate__fadeInUp">{element.description}</p>
                    <a onClick={this.handleButton.bind(this,element.bouton)}  class="btn-get-started animate__animated animate__fadeInUp scrollto a">{element.bouton}</a>
                  </div>
                </div>
              </div>

              )

            ))
           
         }

  
           </div> 

          <a class="carousel-control-prev" href="#heroCarousel" role="button" data-slide="prev">
            <span class="carousel-control-prev-icon icofont-simple-left" aria-hidden="true"></span>
            <span class="sr-only">Previous</span>
          </a>

          <a class="carousel-control-next" href="#heroCarousel" role="button" data-slide="next">
            <span class="carousel-control-next-icon icofont-simple-right" aria-hidden="true"></span>
            <span class="sr-only">Next</span>
          </a>

        </div>
      </section>
      
      

      {/* Publicit */}
      <section >
        <div class="container-fluid">
          <div class="row">
            <div class="col-lg-12 order-1 order-lg-2 text-center">
            {
              (this.state.pub.multimedia!=undefined)?
                (
                <a href={this.state.pub.urlPub} target="_blank">
                  <img src={this.state.pub.multimedia.file} class="img-fluid" alt="Photo" style={{width:"990px",height:"300px"}} /> 
                </a>
                ):null
               
            }
            </div>
          </div>
        </div>
      </section>


      {/* A propos */}
      <section id="about" class="about">
       

          <div class="section-title">
            <h2>A propos</h2>
          </div>

          <div class="row">
            
            <Col md="12" lg="6" sm="12" class="col-lg-6 pt-4 pt-lg-0 order-2 order-lg-1 content text-center">
              <h3 class="text-center">{this.state.titreApropos}</h3>
              <p className="text-center">{this.state.textApropos}
              </p>
            </Col>

            <div class="col-lg-6 col-md-12 col-sm-12 order-1 order-lg-2">
              {/* <img src={aboutImg} class="img-fluid" alt=""/> */}
              <div className="embed-responsive embed-responsive-16by9 mb-4">
                <iframe className="embed-responsive-item" src={this.state.video} height="300" width="300" allowfullscreen frameborder="4"></iframe>
               </div>
            </div>

          </div>
        
      </section>
      
      <hr/>


      {/* Tournois */}
      {
      (this.state.tournoi_entreprise.length || this.state.tournoi_ecole.length)?
      <section id="tournois">
        

          <div class="section-title">
            <h2>Tournois  venir</h2>
            {/* <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p> */}
          </div>

          <div class="row">
            {
              this.state.tournoi_ecole.map((tourn,idx)=>(

              
              (idx<3)?
              (
              <Col lg="4" md="4" sm="12" className="mt-5">
                <Card className="block" id="block" style={{borderRadius:"25px",borderColor:"#4f8f3f",borderWidth:"5px",backgroundColor:"#FFFFF"}}>
                  <CardBody>
                
                <figure class="terrain" className="text-center">
                  <span style={{backgroundColor:"#194D33",textAlign:'center',float:'center'}} class="badge rounded-pill text-center">{tourn.nom}</span>
                  {
                    (tourn.multimedia!=null)?
                      (<img style={{width:"540px",height:"250px"}} src={tourn.multimedia.file} class="img-fluid" alt=""/>):
                    (<img style={{width:"540px",height:"250px"}} src={portfolio2} class="img-fluid" alt=""/>)
                  }
                  {/* <figcaption class="text-center mt-2 font-weight-bold">{this.state.tournoi_ecole[0].nom}</figcaption> */}
                </figure>
                <div class="row">
                  <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                    <Button
                      color="success"
                      className="font-16 btn btn-success float-left"
                      block
                      onClick={this.handleDetailTournoisEcole.bind(this,idx)}
                    >
                      Dtails
                    </Button>
                  </div>
                  <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                    <Button
                      color="success"
                      className="font-16 btn btn-success float-right"
                      block
                      onClick={this.handleParticiperTournoisEcole.bind(this,idx)}
                    >
                      Participer
                    </Button>
                  </div>
                </div>
                </CardBody>
                </Card>
              </Col>
            ):null))
             }
            { 
            this.state.tournoi_entreprise.map((tourn,idx)=>(
              (idx<3)?
              (
              <Col lg="4" md="4" sm="12" className="mt-5">
              <Card className="block" id="block" style={{borderRadius:"25px",borderColor:"#4f8f3f",borderWidth:"5px",backgroundColor:"#FFFFF"}}>
              <CardBody>
              
              <figure class="terrain" className="text-center">
                <span style={{backgroundColor:"#194D33",textAlign:'center',float:'center'}} class="badge rounded-pill text-center">{tourn.nom}</span>
                {
                  (tourn.multimedia!=null)?
                    (<img style={{width:"540px",height:"250px"}} src={tourn.multimedia.file} class="img-fluid" alt=""/>):
                  (<img style={{width:"540px",height:"250px"}} src={portfolio2} class="img-fluid" alt=""/>)
                }
                {/* <figcaption class="text-center mt-2 font-weight-bold">{this.state.tournoi_entreprise[0].nom}</figcaption>  */}
              </figure>
              <div class="row">
                <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                  <Button
                    color="success"
                    className="font-16 btn btn-success float-left"
                    block
                    onClick={this.handleDetailTournoisEntreprise.bind(this,idx)}
                  >
                    Dtails
                  </Button>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                  <Button
                    color="success"
                    className="font-16 btn btn-success float-right"
                    block
                    onClick={this.handleParticiperTournoisEntreprise.bind(this,idx)}
                  >
                    Participer
                  </Button>
                </div>
              </div>
            </CardBody>
            </Card>
          </Col>):null))
          }
          </div>
          <div class="text-center justify-content-center mt-5">
            <a onClick={this.detailTournois} class="a get-started-btn btn-success text-white">Voir la liste des tournois</a>
          </div>
          <hr/>
      </section>:null
     }




     
      {/* Nos Terrain */}

      <section id="terrains">
        

          <div class="section-title">
            <h2>Nos terrains</h2>
            {/* <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p> */}
          </div>

          <div class="row">
           
          {
              this.state.terrain.map((element,idx)=>(
                (idx<=6)?
                (
                  <Col lg="4" md="6" sm="12" className="mt-5">
                    <Card className="block" id="block" style={{borderRadius:"25px",borderColor:"#4f8f3f",borderWidth:"5px",backgroundColor:"#FFFFF"}}>
                      <CardBody>
                        
                        <figure class="terrain" className="text-center">
                          <span style={{backgroundColor:"#194D33",textAlign:'center',float:'center'}} class="badge rounded-pill text-center">{element.nom+" - "+element.adresse}</span>
                          { 
                            element.multimedias.length?
                            (<img style={{width:"350px",height:"200px"}} src={element.multimedias[0].file} class="img-fluid" alt=""/>):
                            (<img style={{width:"350px",height:"200px"}} src={terrain} class="img-fluid" alt=""/>)
                          }
                          {/* <figcaption class="text-center mt-2 font-weight-bold">{element.nom+" - "+element.adresse}</figcaption> */}
                        </figure>
                        <div class="row">
                          <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                            {/* <a onClick={this.handleTerrainClick.bind(this,element)} block class="get-started-btn  text-center">Dtails</a> */}
                            <Button
                              color="success"
                              className="font-16 btn btn-success float-left"
                              block
                              onClick={this.handleTerrainClick.bind(this,element)}
                            >
                              Dtails
                            </Button>
                          </div>
                          <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                            {/* <a onClick={this.handleReserverTerrain.bind(this,element)} class="get-started-btn  text-center">Rserver</a> */}
                            <Button
                              color="success"
                              className="font-16 btn btn-success float-right"
                              block
                              onClick={this.handleReserverTerrain.bind(this,element)}
                            >
                              Rserver
                            </Button>
                          </div>
                        </div>
                      </CardBody>
                    </Card>
                  </Col>
                ):null

              ))
                        

            }
          </div>
          <div className="text-center align-items-center mt-5">
            <a onClick={this.handleVoirTerrain} class="get-started-btn btn-success text-white a">Voir plus de terrains</a>
          </div>
       
      </section>

      <hr/>


    {/* Services */}
    <section id="services" class="services">
       

       <div class="section-title">
         <h2>Services</h2>
         {/* <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p> */}
       </div>

       <div class="row mt-2">
         {
           this.state.service.map((element,idx)=>(

             <div class="col-lg-4 col-md-6 d-flex align-items-stretch mt-4 mt-md-0" data-aos="zoom-in" data-aos-delay="200">
             <div class="icon-box iconbox-orange ">
               

               { 
                      element.multimedia.file?
                     (<img src={element.multimedia.file} class="img-fluid" alt="" style={{width:"310px", height:"208px"}} />):
                     ( <div class="icon">
                         <svg width="100" height="100" viewBox="0 0 600 600" xmlns="http://www.w3.org/2000/svg">
                           <path stroke="none" stroke-width="0" fill="#f5f5f5" d="M300,582.0697525312426C382.5290701553225,586.8405444964366,449.9789794690241,525.3245884688669,502.5850820975895,461.55621195738473C556.606425686781,396.0723002908107,615.8543463187945,314.28637112970534,586.6730223649479,234.56875336149918C558.9533121215079,158.8439757836574,454.9685369536778,164.00468322053177,381.49747125262974,130.76875717737553C312.15926192815925,99.40240125094834,248.97055460311594,18.661163978235184,179.8680185752513,50.54337015887873C110.5421016452524,82.52863877960104,119.82277516462835,180.83849132639028,109.12597500060166,256.43424936330496C100.08760227029461,320.3096726198365,92.17705696193138,384.0621239912766,124.79988738764834,439.7174275375508C164.83382741302287,508.01625554203684,220.96474134820875,577.5009287672846,300,582.0697525312426"></path>
                         </svg>
                         { idx===2?
                           <i class="bx bx-md bx-football"></i>:
                           <i class="bi bi-trophy"></i>
                         }
                         
                       </div>
                   )
                     }
                
                
               <h4><a href="#">{element.titre}</a></h4>
               <p>{element.description}</p>
             </div>
           </div>
           ))
         
         }
        

       </div>
    </section>
   
    <hr/>


      
      {/* Resultats */}
    {
        (this.state.status_resultat_ecole || this.state.status_resultat_entreprise)? 
      <section id="resultats" ref={(ip) => this.myInp = ip} class="clients">
        

          <div class="section-title">
            <h2>Rsultats des matchs du tournois en cours</h2>
            {/* <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p> */}
          </div>
          <p class="h4 text-center">Tournois de l'anne en cours</p>
          <div class="row">
          {
          this.state.status_resultat_ecole?  
          (<div class="card mb-2 col-md-6">
            <p class="h5 text-center mt-2">Tournois inter-coles</p>
            <div class="card-body">
              <div class="row text-center">
                <div class="col-lg-4 col-md-4 col-sm-4">
                  <p class="h4 font-weight-bold">{this.state.resultatEcole.equipeVisites[0].nomEquipe}</p>
                </div>
                <div class="col-lg-4 col-md-4 col-sm-4">
                  <p class="score h4 bg-success text-white rounded p-2 font-weight-bold">{this.state.resultatEcole.scoreVisite} - {this.state.resultatEcole.scoreVisiteur}</p>
                  <p class="h6">Date du {this.state.resultatEcole.equipeVisites[0].dateDebutTournoi} 20:00</p>
                  <p class="h6">Tournoi: <span class="font-italic"> {this.state.resultatEcole.equipeVisites[0].nomTournoi}</span></p>
                </div>
                <div class="col-lg-4 col-md-4 col-sm-4">
                  <p class="h4 font-weight-bold">{this.state.resultatEcole.equipeVisiteurs[0].nomEquipe}</p>

                  <p class="autres-matchs-tournois"><a onClick={this.handleResultat} class="lien-autres-match font-weight-bold a">Voir les autres matchs</a></p>
                </div>
              </div>
            </div>
          </div>):null
          }
          {
           this.state.status_resultat_entreprise?  
          (<div class="card mb-2 col-md-6">
            <p class="h5 text-center mt-2">Tournois inter-entreprises</p>
            <div class="card-body">
              <div class="row text-center">
                <div class="col-lg-4 col-md-4 col-sm-4">
                  <p class="h4 font-weight-bold">{this.state.resultatEntreprise.equipeVisites[0].nomEquipe}</p>
                </div>
                <div class="col-lg-4 col-md-4 col-sm-4">
                  <p class="score h4 bg-success text-white rounded p-2 font-weight-bold">{this.state.resultatEntreprise.scoreVisite} - {this.state.resultatEntreprise.scoreVisiteur}</p>
                  <p class="h6">Date du {this.state.resultatEntreprise.equipeVisiteurs[0].dateDebutTournoi} 20:00</p>
                  <p class="h6">Terrain: <span class="font-italic"> {this.state.resultatEntreprise.equipeVisiteurs[0].nomTournoi}</span></p>
                </div>
                <div class="col-lg-4 col-md-4 col-sm-4">
                  <p class="h4 font-weight-bold">{this.state.resultatEntreprise.equipeVisiteurs[0].nomEquipe}</p>

                  <p class="autres-matchs-tournois"><a onClick={this.handleResultat} class="lien-autres-match font-weight-bold a">Voir les autres matchs</a></p>
                </div>
              </div>
            </div>
          </div>):null
         }

        </div>
          {/* <p class="text-right mt-4">
            <a href="#" 
            // style="color: #000;font-weight: bold;"
            >
              Voir les tournois prcdent
            </a>
          </p> */}
      
      
      <hr/>
      </section>:null
    }


      {/* Nos partenaires */}
      {
        this.state.fichier.length?
        <section id="partenaires" class="clients">
        

          <div class="section-title">
            <h2>Nos partenaires</h2>
            {/* <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p> */}
          </div>


          <OwlCarousel 
            items={4} 
            margin={4} 
            autoplay ={true} 
            center={true}
          > {
            this.state.fichier.map((el)=>(
              <div><img className="img" src= {el.multimedia.file}/></div>  
            )) 
            }
             
          </OwlCarousel>
          <hr/>
        </section>:null
        
      }


      


      <section id="contact" class="contact">
        

          <div class="section-title">
            <h2>Contact</h2>
            {/* <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p> */}
          </div>

          <div class="row">

            <div class="col-lg-5 d-flex align-items-stretch">
              <div class="info">
                <div class="address">
                  <i class="icofont-google-map"></i>
                  <h4>Adresse:</h4>
                  <p>{this.state.adresse}</p>
                </div>

                <div class="email">
                  <i class="icofont-envelope"></i>
                  <h4>Email:</h4>
                  <p>{this.state.email}</p>
                </div>

                <div class="phone">
                  <i class="icofont-phone"></i>
                  <h4>Tlphone:</h4>
                  <p>{this.state.telephone}</p>
                </div>

                {/* <iframe 
                  src='https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d12097.433213460943!2d-74.0062269!3d40.7101282!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xb89d1fe6bc499443!2sDowntown+Conference+Center!5e0!3m2!1smk!2sbg!4v1539943755621'
                  frameborder="0" style="border:0; width: 100%; height: 290px;" allowfullscreen
                >
                </iframe> */}

              </div>

            </div>

            <div class="col-lg-7 mt-5 mt-lg-0 d-flex align-items-stretch">
              <div class="php-email-form">
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="name">Votre nom</label>
                    <input type="text" name="name" class="form-control" id="nom" data-rule="minlen:4" data-msg="Veuillez saisir au moins 4 caractres" />
                    <div class="validate"></div>
                  </div>
                  <div class="form-group col-md-6">
                    <label for="name">Votre Email</label>
                    <input type="email" class="form-control"  id="email" data-rule="email" data-msg="Veuillez entrer une adresse mail valide" />
                    <div class="validate"></div>
                  </div>
                </div>
                <div class="form-group">
                  <label for="name">Objet</label>
                  <input type="text" class="form-control" id="objet" data-rule="minlen:4" data-msg="Veuillez saisir au moins 8 caractres pour le sujet" />
                  <div class="validate"></div>
                </div>
                <div class="form-group">
                  <label for="name">Message</label>
                  <textarea class="form-control" id="message" rows="10" data-rule="required" data-msg="Veuillez crire quelque chose pour nous"></textarea>
                  <div class="validate"></div>
                </div>
                <div class="mb-3">
                  <div class="loading">Chargement</div>
                  <div class="error-message"></div>
                  <div class="sent-message">Votre message a t envoy. Merci!</div>
                </div>
                <div id="initial" class="text-center"><button onClick={this.handleSendMessage} id="submit">Envoyer</button></div>
                <div id="chargement" style={{display:"none"}} class="text-center"><button id="submit"><span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span> Envoi...</button></div>
                <div id="msg" class="sent-message">Votre message a t envoy. Merci!</div>
              </div>
            </div>

          </div>

      </section>


    </CFade>):(<>{this.state.page}</>)
  }
    </Suspense>
    </CContainer>

    </>
  )
}
}

export default AccueilContent
